import React, { Component } from 'react'
import * as tf from '@tensorflow/tfjs'
import Grid from '@material-ui/core/Grid'
import './Demo.css'

import NormalDistribution from 'normal-distribution'

class Demo extends Component {
  constructor(props) {
    super(props)
    this.state = {
      thresholds: {
        men: 50,
        women: 50
      },
      acceptanceRates: {
        men: 0,
        women: 0
      }
    }
  }

  setThreshold(group, value) {
    const { thresholds, acceptanceRates } = this.state

    this.setState({
      thresholds: {
        ...thresholds,
        [group]: value
      },
      acceptanceRates: {
        ...acceptanceRates,
        [group]: this.distributions[group].cdf(value)
      }
    })
  }

  componentDidMount() {
    const { thresholds } = this.state
    this.distributions = {
      men: new NormalDistribution(50, 15),
      women: new NormalDistribution(60, 20)
    }
    this.setState({
      acceptanceRates: {
        men: this.distributions.men.cdf(thresholds.men),
        women: this.distributions.women.cdf(thresholds.women)
      }
    })
  }

  render() {
    const {
      done,
      thresholds,
      acceptanceRates
    } = this.state

    return (
      <Grid item xs={10}>
        <div className="article-section no-padding">
          <div className="dp-demo-wrap">
            <div className="dp-form">
              <div className="dp-input-group">
                <label htmlFor="slider-men" className="dp-form-label">Men</label>
                <input
                  id="slider-men"
                  className="dp-input"
                  type="range"
                  min="0"
                  max="100"
                  value={thresholds.men}
                  onChange={e => this.setThreshold('men', e.target.value)}
                />
                <p className="dp-input-value">{thresholds.men}</p>
                <p className="dp-acceptance-rate">{Math.round(acceptanceRates.men * 100)}</p>
              </div>
              <br/>
              <div className="dp-input-group">
                <label htmlFor="slider-women" className="dp-form-label">Women</label>
                <input
                  id="slider-women"
                  className="dp-input"
                  type="range"
                  min="0"
                  max="100"
                  value={thresholds.women}
                  onChange={e => this.setThreshold('women', e.target.value)}
                />
                <p className="dp-input-value">{thresholds.women}</p>
                <p className="dp-acceptance-rate">{Math.round(acceptanceRates.women * 100)}</p>
              </div>
            </div>
          </div>
        </div>
      </Grid>
    )
  }
}

export default Demo
