import React, { Component } from 'react'
import Grid from '@material-ui/core/Grid'
import './MagazinePage.css'

class MagazinePage extends Component {
  render() {
    const { content } = this.props

    return [
      <Grid container item xs={10} key="main-content">
        {content.paddingLeft > 0 && <Grid item md={content.paddingLeft} />}
        <Grid item md={12 - content.paddingLeft - content.paddingRight}>
          <div className="article-section">
            <Grid container item xs={12} spacing={4}>
              <Grid item xs={content.title.span}>
                <h2>{content.title.text}</h2>
              </Grid>
            </Grid>
            <Grid container item xs={12} spacing={4}>
              <Grid item xs={content.img.span}>
                <img 
                  className="magazine-image" 
                  src={content.img.url} 
                  alt={content.img.alt} 
                />
              </Grid>
            </Grid>
            <Grid container item spacing={4} className="magazine-text-wrap">
              {content.text.map(column => {
                switch (column.type) {
                case 'quote':
                  return (
                    <Grid item xs={column.span}>
                      <h4>{column.text}</h4>
                    </Grid>
                  )
                case 'text':
                  return (
                    <Grid item xs={column.span}>
                      <p>{column.text}</p>
                    </Grid>
                  )
                default:
                  return null
                }
              })}
            </Grid>
          </div>
        </Grid>
        {content.paddingRight > 0 && <Grid item md={content.paddingRight} />}
      </Grid>,
      <Grid item xs={10} className="no-padding magazine-divider" key="divider"><hr /></Grid>
    ]
  }
}

export default MagazinePage
